*{margin:0;padding:0}html{height:100%}body{box-sizing:border-box;height:100%;font-family:'Lato', sans-serif;color:#000000;background-color:#ffffff}#root{position:relative}#site-container .push-main{margin-top:172px}#site-container .mobile-menu-transition{transition:1s}#header{position:relative;height:50px;line-height:50px}#header #burgerMenu{display:none}#header nav{width:70%}#header nav>a{font-size:14px;margin:0 10px;padding:18px 20px;text-decoration:none;color:#404040;transition:.5s}#header nav>a:hover{background-color:#f5f5f5;color:#4169e1}#header #headerIconsContainer{position:absolute;right:0;top:0}#header #headerIconsContainer .header-icon{padding:5px;background:royalblue;border-radius:50%;color:white;margin:0 10px;border:3px outset white;transition:.5s}#header #headerIconsContainer .header-icon:hover{cursor:pointer;box-shadow:0 0 1px black}@media screen and (max-width: 450px){#header #burgerMenu{display:inline;margin-left:15px;font-size:25px}#header nav{width:100%;height:0;overflow:hidden;background-color:white}#header nav>a{display:block;text-align:center;margin:0}#header .push-nav{height:172px}#header #headerIconsContainer{display:none}}footer{background:#0c1015;color:#2f3f52;text-align:center;font-size:12px}footer #contactMe{display:flex;align-items:center;flex-direction:row;justify-content:space-between}footer #contactMe span{margin-left:10px}footer #contactMe a{text-decoration:none;color:#2f3f52;font-size:12px;padding:10px;transition:.8s}footer #contactMe a:hover{background:linear-gradient(to right, #193997, #062c5a);color:#f8f8ff}#homeContainer .home-section-header{letter-spacing:4px;font-size:13px;font-weight:normal}#homeContainer .common-sections{height:100px;padding:70px 60px;display:flex;justify-content:center;align-items:center;flex-direction:column}#homeContainer #homeSectionOne{height:700px;background-image:url(data:image/jpeg;base64,);background-size:cover;background-position:center;background-repeat:no-repeat;opacity:0.85}#homeContainer #homeSectionOne #developerCaptionContainer{position:relative;top:130px}#homeContainer #homeSectionOne .name-caption{position:absolute;top:206px;right:0;width:30%;left:-50px;padding:15px;margin:0 auto;box-shadow:0 0 80px black;border-top-left-radius:6px;border-bottom-left-radius:6px;border-top-right-radius:6px;border-bottom-right-radius:6px;text-align:center;background:rgba(0,0,0,0.87);color:#615b5b;letter-spacing:20px;font-size:12px;transform:rotate(-11deg)}#homeContainer #devInfo{display:flex;align-items:center;justify-content:space-between;padding:80px 60px;background-color:#11161D;color:#2f3f52}#homeContainer #devInfo .about-me-article{width:48%}#homeContainer #devInfo h1{color:#888787;margin:12px 0 35px 0}#homeContainer #devInfo p{word-spacing:5px;line-height:35px}#homeContainer #homeSectionTwo{background-color:#f8f8ff}#homeContainer #homeSectionTwo h1{position:relative;top:-30px;color:#000000}#homeContainer #homeSectionTwo #technologiesListContainer{text-align:center;padding:10px;width:100%;overflow-x:scroll}#homeContainer #homeSectionTwo .colored{font-size:25px;margin:0 22px}#homeContainer #expSection{background-color:#11161D}#homeContainer #expSection h1{position:relative;top:-30px;color:#888787}#homeContainer #expSection .company-img{margin:0 55px}@media screen and (max-width: 450px){#homeContainer *{font-size:10px}#homeContainer .home-section-header{font-size:10px}#homeContainer #homeSectionOne .name-caption{line-height:25px;text-align:center;left:0;width:80%}#homeContainer #devInfo{flex-direction:column}#homeContainer #devInfo .about-me-article{width:auto}#homeContainer #devInfo #leftArticle{margin-bottom:20px}#homeContainer #expSection #companiesContainer{display:flex;flex-wrap:wrap;justify-content:space-between;width:100%}#homeContainer #expSection .company-img{margin:0}}
